import React from "react";
import OraSwitcher from "eshop/modules/core/components/ui/OraSwitcher";
import PropositionForDeviceMobileView from "eshop/modules/simfree/components/PropositionForDeviceMobileView"

import PropositionForDeviceTabletDesktopView from "eshop/modules/simfree/components/PropositionForDeviceTabletDesktopView"


const OfferProductListMobileView = (props) => {
    let proposition=props.offer;

    return (

        <OraSwitcher key={props.index+"_switcher_mobile"} onChangeHandler={props.onChange} isSelected={props.isSelected} className="u-margin-l u-no-padding-r u-border" switcherClassName="  opl-switcher-list__item u-cursor-pointer u-padding-all-l">
            <div className={"u-large-hide u-medium-hide"}>

                <PropositionForDeviceMobileView offer={proposition} {...props} isSelected={props.isSelected} key={"mobile_"+proposition.id}
                                                index={props.index}/>
            </div>
            <div className={"u-small-hide"}>

                <PropositionForDeviceTabletDesktopView offer={proposition} {...props} isSelected={props.isSelected} key={"tablet_"+proposition.id}
                                                       index={props.index}/>
            </div>
        </OraSwitcher>

)}

export default OfferProductListMobileView;
